.header {
  background: #EDEDE9;
  @include mx980 {
    position: fixed;
    width: 100%;
    z-index: 1000;
  }
  

  &__container {
    width: 90%;
    margin: 0 auto;
    max-width: 1920px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;

  }

  &__checkbox {
    display: none;
  }

  &__label {
    font-size: 30px;
    display: none;
    cursor: pointer;
    @include mx980 {
      display: block;
    }
  }

  &__logo {
    line-height: 50px;
    font-size: 40px;
    font-weight: bold;
    letter-spacing: 3px;
    @include sp {
      font-size: 32px;
    }
  }

  &__menu {
    display: none;
    @include mx980 {
      display: block;
    }
  }

  &__menu-container {
    filter: invert(43%) sepia(11%) saturate(1062%) hue-rotate(33deg) brightness(90%) contrast(85%);
  }

  &__nav {
    height: 50px;
    color: 	#656d4a;
    background: #EDEDE9;
    z-index: 11;
    @include mx980 {
      display: none;
    }

  }

  &__navlink {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  &__navlinks {
    padding: 9px 40px ;
    line-height: 20px;
    height: 20px;
    font-size: 24px;
    text-transform: uppercase;

    &:hover {
      color: #434930;
    }
  }

  &__link-sp {
    font-size: 1.6rem; 
    font-weight: $bold;
    display: block;
    padding: 20px 20px;
    text-align: center;
    border-top: 1px solid #e4e4e4;
   }

   &__links-sp {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    width: 100%;
    top: 100%;
    transition: all 0.3s;
  }

  .active {
    @include mx980 {
    position: relative;
    display: block;
    visibility: visible;
    opacity: 1;      
    }
  }
}

