.main {
  background-color: #D6CCC2;
  color: #656d4a;
  height: 100vh;
  width: 100%;
  @include sp {
    height: unset;
  }

  &__container {
    display: flex;
    align-items: center;
    height: 100vh;
    width: 100%;
    margin: 0 auto;
    max-width: 1920px;
    @include sp {
      flex-direction: column;
      height: unset;
    }
  }

  &__content {
    width: 50%;
    padding: 0 5%;
    @include sp {
      width: 100%;
      padding: 30px 5%;
      padding: 100px 5% 50px;
    }
  }

  &__hero {
    width: 50%;
    height: 100%;
    overflow: hidden;
    @include sp {
      width: 100%;
      height: 300px;
    }
  }

  &__hero-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    font-size: 50px;
    position: relative;
    z-index: 1;
    margin-bottom: 25px;
    line-height: 72px;
    // font-weight: 100;
    @include sp {
      margin: 0;
      font-size: getsp375(50px);
      line-height: 32px;
    }
  }

  &__text {
    max-width: 520px;
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 34px;
    font-weight: 300;
    @include sp {
      margin: 10px 0 20px 0;
      font-size: getsp375(24px);
      line-height: 16px;
    }
  }

  &__btn-wrap {
    display: flex;
    width: 100%;
    justify-content: start;
  }

  &__btn {
    width: 200px;
    padding: 10px 30px;
    line-height: 20px;
    border-radius: 50px;
    font-size: 18px;
    font-weight: 400;
    // text-transform: none;
    text-align: center;
    background: #656d4a;
    color: #D6CCC2;
    justify-content: center;
    border: 1px solid #656d4a;  
    @include sp {
      font-size: 14px;
      width: unset;
      padding: 5px 20px;
    }

    &:hover {
      background: #D6CCC2;
      color: #656d4a ;
      transition: .5s;
    }
  }
}
