.third {
  background: lightgray;
  color: #656d4a ;
  text-align: center;

  &__container {
    width: 90%;
    height: 100%;
    padding: 50px 0;
    max-width: 1920px;
    margin: 0 auto;
    @include sp {
      width: 100%;
      padding: 50px 5%;
    }
  }

  &__title {
    font-size: 24px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    @include sp {
      font-size: 14px;
    }
  }

  &__featuredWork {
    display: flex;
    padding: 30px  0;
    @include sp {
      flex-direction: column;
    }
  }

  &__layer{
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    background: rgb(214, 204, 194,.8);
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: .5s ease;
    border-radius: 50px;
  }

  &__featuredWork-first{
    box-shadow: 0 5px 15px 0 rgb(0 0 0 / 15%);
    flex-basis: 30%;
    margin: 10px;
    position: relative;
    color: black;
    position: relative;
    border-radius: 50px;
    overflow: hidden;
    @include sp {
      border-radius: 20px;
    }

    &:hover .third__layer{
        bottom: 0;
        height: 100%;
        border-radius: 50px;
        @include sp {
          border-radius: 20px;
        }
    }
  }

  &__featuredWork-img-wrap {
    height: 700px;
    border-radius: 50px;
    overflow: hidden;
    @include sp {
      height: 400px;
      border-radius: 20px;
    }
  }

  &__featuredWork-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }


  &__text {
    white-space: nowrap;
    color: #656d4a;
    font-size: 50px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include sp {
      font-size: 24px;
    }
  }

  &__btn {
    border: 1px solid #656d4a;
    background: #656d4a;
    color: lightgray;
    border-radius: 50px;
    text-align: center;
    justify-content: center;

    &:hover {
      background: lightgray ;
      color: #656d4a;
    }
  }
}
