.footer {
  width: 100%;
  margin: auto;
  text-align: center;
  color: #D5BDAF;
  background:#656d4a ;
  position: relative;
  display: block;

  &__container {
    display: flex;
    justify-content: space-between;
    gap: 50px;
    width: 100%;
    max-width: 1000px;
    text-align: start;
    margin: 0 auto;
    padding: 50px 80px 0;
    @include sp {
      padding: 30px 5% 0;
      gap: 0;
      flex-direction: column;
      text-align: center;
    }

  }

  &__column-item {
    margin-bottom: 30px;
    @include sp {
      margin-bottom: 15px;
    }
  }

  &__text {
    font-size: 32px;
    @include sp {
      font-size: 16px;
    }
  }

  &__subtext {
    font-size: 24px;
    @include sp {
      font-size: 12px;
    }
  }

  &__icon {
    color: #D5BDAF;
    margin: 0 5px;
    cursor: pointer;
    color: #D5BDAF;
    margin: 0 13px;
    font-size: 30px;
    padding: 19px 0;
    @include sp {
      font-size: 24px;
      margin: 10px 10 0 10px;
      padding: 5px 0 0 0;
    }
  }

  &__copyright{
    display:block;
    z-index: 100;
    color: #D5BDAF;
    width: 100%;
    font-size: 16px;
    border-top: 1px solid #D5BDAF;
    padding: 20px 0;
    background:#656d4a ;
    @include sp {
      font-size: 12px;
      padding: 15px 0;
    }
  }
}

