.btn {
  width: 200px;
  margin-top: 20px ;
  padding: 10px 30px;
  line-height: 20px;
  border-radius: 50px;
  font-size: 15px;
  font-weight: 400;
  text-transform: none;
  text-align: center;
  background: #656d4a;
  color: #D6CCC2;
  justify-content: center;
  border: 1px solid #656d4a;    
  @include sp {
    width: unset;
    font-size: 12px;
    padding: 5px 30px;

  }
  
  &:hover {
    background: #D6CCC2;
    color: #656d4a ;
    transition: .5s;
  }
}
