.first {
  background: #F5EBE0;
  color: #656d4a;
  // height: 820px;
  width: 100%;
  @include sp {
    height: unset;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;  
    width: 90%;
    max-width: 1920px;
    margin: 0 auto;
    padding: 50px 0;
    @include sp {
      flex-direction: column-reverse;
      width: 100%;
      padding: 50px 5%;
    }
  }

  &__content {
    margin: 0 80px;
    width: 50%;
    @include sp {
      width: 100%;
    }
  }

  &__image-wrap {
    width: 50%;
    height: 100%;
    @include sp {
      width: 100%;
      height: 300px;
    }
  }

  &__image {
    width: 100%;
    height: auto;
    box-shadow: 0 5px 15px 0 rgb(0 0 0 / 15%);
    @include sp {
      height: 100%;
      object-fit: cover;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5px;
    @include sp {
      gap: 20px;
      margin: 10px 0 25px;
    }
  }

  &__text {
    padding: 10px 0;
    margin: 20px 20px 20px 0;
    border-top: 1px solid #656d4a;
    font-size: 15px;
    @include sp {
      font-size: 12px;
      padding: 5px 0;
      margin: 0;

    }
  }

  &__title {
    margin-top: 0px;
    margin-bottom: 25px;
    font-size: 40px;
    line-height: 50px;
    font-weight: lighter;
    @include sp {
      font-size: 24px;
      line-height: 26px;
    }
  }

  &__subtitle {
    font-size: 18px;
    @include sp {
      font-size: 14px;
    }
  }
}
