.second {
  background: #D5BDAF;
  color: #656d4a;
  height: 700px;
  text-align: center;
  position: relative;
  @include sp {
    height: unset;
  }

  &__container {
    display: flex;
    width: 90%;
    max-width: 1920px;
    min-height: 700px;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    @include sp {
      min-height: unset;
      padding: 50px 0 80px 0;
      width: 100%;
    }
  }

  &__content{
    display: flex;
    flex-direction: column;
    
  }

  &__title {
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    margin-bottom: 10px;
    line-height: 52px;
  }

  &__text {
    font-size: 40px;
    line-height: 52px;
    margin-bottom: 30px;
    font-weight: lighter;
    @include sp {
      font-size: 24px;
      line-height: 28px;
    }
  }

  &__img-left {
    z-index: 10;
    position: absolute;
    width: 150px;
    height: 230px;
    top:550px;
    left: 0;
    @include sp {
      bottom: -50px;
      top: unset;
      width: 80px;
      height: 130px;
    }
  }

  &__img-right {
    width: 180px;
    z-index: 10;
    position: absolute;
    top:50px;
    right: 0;
    @include sp {
      top: 20px;
      width: 100px;
    }
  }

  &__btn {
    border: 1px solid #656d4a;
    border-radius: 50px;
    text-align: center;
    display: block;
    width: 100%;

    &:hover {
      background: #D5BDAF;
    } 
  }
}
